/**
*
* Testing
*
**/
.padding-major {
  padding: 1.2rem;
}

.padding-card {
  padding: 1.5rem 1rem;
}

.margin-bottom-small {
  margin-bottom: 0.8rem;
}

.icon-style {
  font-size: 1.2rem;
  color: #dddedf;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 75.5%;
}

body {
  user-select: none;
  overscroll-behavior: none;
  font-family: "Josefin Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
}
@media (min-width: 768px) and (max-width: 1365px) {
  body {
    font-size: 59%;
  }
}
@media (min-width: 360px) and (max-width: 767px) {
  body {
    font-size: 54%;
  }
}

.container {
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%;
  height: 96vh;
  background-image: linear-gradient(#dddedf, #f3f7f9);
}

.inner-container {
  position: absolute;
  padding: 2rem;
  top: 2.5%;
  left: 0;
  width: 96%;
  height: 85vh;
  background-color: #dddedf;
  display: flex;
  flex-direction: column;
  flex-flow: wrap;
}

.options {
  width: 25%;
  background-color: #ffffff;
  height: 100%;
  border-radius: 3px;
}
.options-option {
  border-bottom: 1px solid #dddedf;
  display: flex;
  flex-direction: column;
  flex-flow: wrap;
  padding: 1rem 1.5rem;
}
.options-icons {
  color: #dddedf;
  margin-right: 0.8rem;
}
.options-heading {
  color: black;
  margin-bottom: 0.4rem;
}
.options-brief {
  color: #dddedf;
}

.dashboard {
  width: 70%;
  background-color: #ffffff;
  height: 100%;
  border-radius: 3px;
  margin-left: 2rem;
}

.upper {
  border-bottom: 1px solid #dddedf;
}

.color-blue {
  color: #1594eb;
}

.integration {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.integration-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 30%;
}

.search-bar {
  border: none;
  width: 100%;
  height: 3rem;
  outline: none;
  font-size: 1.2rem;
  font-family: inherit;
  font-weight: 500;
  letter-spacing: 0.2px;
  transition: all 0.3s;
  padding-left: 1rem;
  color: #dddedf;
  border-radius: 5px;
  background-color: #f3f7f9;
}
.search-bar:focus {
  box-shadow: 8px 9px 16px rgba(173, 183, 199, 0.6),
    -8px -9px 16px rgba(255, 255, 255, 0.5);
}
.search-bar::-webkit-input-placeholder {
  color: #dddedf;
  font-size: 1.1rem;
  padding-left: 0.5rem;
  opacity: 0.8;
}

.card-alignment {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.card {
  position: relative;
  padding: 1rem 1.2rem;
  border-radius: 2px;
  display: inline-block;
  width: 30%;
  height: 19rem;
  border: 1px solid #f3f7f9;
  background-color: #ffffff;
  transition: all 0.2s;
}
.card-img {
  height: 38%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.card-company {
  position: absolute;
  top: 50%;
}
.card-name {
  color: black;
  font-weight: bold;
  margin-bottom: 1rem;
}
.card-detail {
  color: #dddedf;
  margin-bottom: 1rem;
}
.card-lower {
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-sizing: border-box;
  width: 90%;
}
.card-like-delete {
  display: flex;
  flex-direction: row;
}
.card-connected {
  color: #dddedf;
  transition: all 0.2s;
}
.card-delete {
  transition: all 0.2s;
  opacity: 0;
}
.card:hover {
  box-shadow: 1px 1px 1px #f3f7f9;
}
.card:hover .card-connected {
  color: #1594eb;
}
.card:hover .card-delete {
  opacity: 1;
}

.img {
  display: block;
  margin: 0 auto;
  width: 5rem;
}
.favorite-list {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
.list {
  list-style: none;
}
.icon-blue {
  color: #1594eb;
}
.border-right {
  border-right: 2.5px solid #1594eb;
}
